*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Arial', sans-serif;
  background-color: #fff;
  color: #10264b;
  position: relative;
}

.app {
	display: flex;
	flex-direction: column;
	align-items: center;
	height: 100vh;
	/* overflow: hidden; */
	/* background-color: rgba(238, 238, 238, 1); */
}
.btn {
	padding: 12px 24px;
	border: 2px solid rgba(0,0,0,0);
	border-radius: 6px;
	cursor: pointer;
	text-transform: uppercase;
	font-size: 0.9rem;
    font-weight: bold;
	transition: all 200ms;
}




/* Car list container styles */
.car-list-container {
	display: flex;
	flex-direction: column;
	grid-gap: 24px;
	gap: 24px;
	width: 100%;
	justify-content: center;
	align-items: center;
	padding: 24px;
}
.car-list {
	max-width: 1400px;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}

/* .car-list-header {
	align-self: center;
	width: 1400px;
	padding: 0 48px;
} */
.card {
	display: flex;
	flex-direction: column;
	width: 300px;
	margin: 20px;
	box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2);
	line-height: 1.5;
}

.card-body {
	padding: 20px;
	background: #10264b;
	color: #fff;

	border-radius: 0px 0px 12px 12px;
}

.card-title {
	font-size: 1.5rem;
	text-align: center;
	margin-bottom: 20px;
}

.card-img-top {
	height: 200px;
	width: 300px;
	border-radius: 12px 12px 0 0;
}

.btn-card {
	width: 100%;
}

.card-bottom {
	padding-top: 12px;
    height: 60px;
	display: flex;
	justify-content: center;
	align-items: center;
}

.reserved-text {
	padding: 12px 20px;
}

.form-container {
	position: fixed;
	display: flex;
	flex-direction: column;
	grid-gap: 0.5rem;
	gap: 0.5rem;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	z-index: 12;
	overflow: hidden;
}

.form {
	z-index: 999;
}

.form-btn {
	padding: 1rem;
	font-size: 1.6rem;
	color: #fffafa;
	background: rgba(33, 33, 33, 0.9);
	border-style: none;
	border-radius: 10px;
	cursor: pointer;
}

.form-btnClose {
	color: #fffafa;
	border-style: none;
	background: none;
	position: fixed;
	top: 10px;
	right: 10px;
	border-radius: 50%;
	z-index: 999999;
	cursor: pointer;
}

.form-btnIcon {
	background-color: rgba(33, 33, 33, 0.5);
	width: 2.4rem;
	height: 2.4rem;
	border-radius: 50%;
	padding: 0.7rem;
}

.form-background {
	position: fixed;
	background-color: rgba(0, 0, 0, 0.5);
	-webkit-backdrop-filter: blur(2px);
	        backdrop-filter: blur(2px);
	top: 0;
	left: 0;
	z-index: 11;
	height: 100%;
	width: 100%;
}

.form-success {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	grid-gap: 2rem;
	gap: 2rem;
	background-color: #10264b;
	padding: 40px 60px;
	border: solid 1px #fff;
	border-radius: 12px;
	color: white;
	font-size: 20px;

}

.form-successIcon {
	background-color: rgb(90, 169, 42);
	width:6rem;
	height: 6rem;
	border-radius: 50%;
	padding: 0.7rem;
	fill: white;
}

.filter {
	display: flex;
	grid-gap: 12px;
	gap: 12px;
	justify-content: space-between;
	align-items: center;
	max-width: 1400px;
	width: 100%;
	height: 80px;
	padding: 12px 48px;
}

.filter-control{
	width: 100%;
	height: 100%;
	padding: 12px 8px 0 12px;
	border: 1px solid #10264b;
	border-radius: 12px;
}

.filter-control--checkbox{
	height: 16px;
	width: 16px;
	
}

.filter-input-container{
	min-width: 120px;
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	height: 100%;
	position: relative;
}

.filter-input-container--checkbox{
	min-width: 120px;
	display: flex;
	grid-gap: 12px;
	gap: 12px;
	align-items: center;
	justify-content: center;
	width: 100%;
	height: 100%;
	position: relative;
}

.filter-input-container label{
	font-size: 12px;
	position: absolute;
	top: 8px;
	left: 12px;
	z-index: 2;
}
.checkbox-label{
	/* background-color: rebeccapurple; */
	top: 20px;
	left: 123;
}
/* Navbar styles */
.navbar {
	background-color: #fff;
	display: flex;
	width: 100%;
	justify-content: center;
	flex-basis: 80px;
	z-index: 2;
	box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.3);
}

.navbar-main {
	display: flex;
	width: 100%;
	max-width: 1400px;
	height: 75px;
	justify-content: space-between;
	align-items: center;
	padding: 12px 48px;
}
/* Navbar brand styles */
.navbar-brand {
	color: #fff;
	font-size: 1.6rem;
	font-weight: bold;
	text-decoration: none;
	border: none;
    padding: 8px 12px;
    border-radius: 12px;
    border: 2px solid #10264b;
	background-color: #10264b;
    cursor: pointer;
}

/* Login and Register button styles */
.navbar-nav {
	display: flex;
	grid-gap: 10px;
	gap: 10px;
}

.btn-blue {
	background-color: #10264b;
	color: #fff;
}

.btn-white {
	background-color: #fff;
	color: #10264b;
	border: 2px solid #10264b;
}

.btn-red {
	background-color: #d24141;
	color: #fff;
}

.btn-blue:hover {
	background-color: #fff;
	color: #10264b;
	border: 2px solid #10264b;
}

.btn-red:hover {
	background-color: #fff;
	color: #d24141;
	border: 2px solid #d24141;
}

.login-container {
	display: flex;
	width: 100%;
	height: 100%;
	justify-content: center;
	align-items: center;
}

.login-form {
	font-size: 14px;
	background: #10264b;
	color: #fff;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: flex-start;
	flex-basis: 500px;
	grid-gap: 20px;
	gap: 20px;
	padding: 40px 80px;
	border: solid 1px #fff;
	border-radius: 12px;
	position: relative;
}

.form-group {
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 70%;
	grid-gap: 4px;
	gap: 4px;
}

.form-group label {
	align-self: flex-start;
}

.form-control {
	width: 100%;
	border: none;
	border-radius: 12px;
	padding: 10px 20px;
}


.errorMessage {
	font-size: 12px;
	color: #d24141;
	position: absolute;
	bottom: 10px;
}
.message {
	font-size: 12px;
	color: rgb(90, 169, 42);
	position: absolute;
	bottom: 10px;
}
.btn-login {
	background-color: #fff;
	width: 70%;
}


.btn-login:hover {
	background-color: #10264b;
	color: #fff;
	border: 2px solid white;
}
.user-container {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 90%;
	color: #fff;
}
.header {
	padding: 12px 24px;
}

.user-profile {
	display: grid;
	grid-template-columns: 300px 1fr;
	max-width: 1400px;
	width: 100%;
	height: 100%;
	padding: 48px;
}

.user-profile--dashboard {
	background-color: #10264b;
	display: flex;
	grid-gap: 48px;
	gap: 48px;
	flex-direction: column;
	padding: 16px 6px;
	border-radius: 12px 0px 0px 12px;
}

.user-profile--main {
	border: 4px solid #10264b;
	color: #10264b;
	border-radius: 0 12px 12px 0;
	display: flex;
	grid-gap: 24px;
	gap: 24px;
	flex-direction: column;
    flex: 1 1;
    overflow: auto;
	padding: 12px 12px;
}

.btn-dashboard {
	display: flex;
	justify-content: start;
	align-items: center;
	width: 100%;
	text-decoration: none;
}

.rentals {
	display: flex;
	flex-direction: column;
	grid-gap: 8px;
	gap: 8px;
}

.rental {
	display: grid;
	grid-template-columns: 280px 1fr;
	width: 100%;
	border: 1px solid #10264b;
	border-radius: 12px;
	overflow: hidden;
}

.rental-info {
	display: flex;
	flex-direction: column;
	justify-content: center;
	grid-gap: 6px;
	gap: 6px;
	align-items: start;
	background-color: #f6f6f6;
	padding: 12px;
	border-radius: 12px 0 0px 12px;
}

.rental-info--state {
	padding: 6px 8px;
	border-radius: 4px;
	color: white;
}

.rental-info--state-on {
	background-color: rgb(90, 169, 42);
}

.rental-info--state-off {
	background-color: #d24141;
}

.rental-info--rentalID {
	font-weight: 600;
}

.rental-details {
	display: flex;
	justify-content: start;
	align-items: center;
	grid-gap: 24px;
	gap: 24px;
	background-color: #fff;
	padding: 12px 24px;
	border-radius: 0 12px 12px 0;
}

.rental-details--img {
	max-width: 180px;
	border: 1px solid #ddd;
	border-radius: 12px;
}

.rental-details--info {
	display: flex;
	flex-direction: column;
	justify-content: center;
	grid-gap: 6px;
	gap: 6px;
	align-items: start;
	width: 100%;
	padding: 12px;
	border-radius: 12px 0 0px 12px;
	flex-shrink: 0;
}

.settings-container {
	display: flex;
	width: 100%;
	height: 100%;
	align-items: stretch;
	justify-content: center;
}

.settings-form {
	font-size: 14px;
	background-color: #10264b;
	color: #fff;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: flex-start;
	flex-basis: 100%;
	grid-gap: 20px;
	gap: 20px;
	padding: 40px 80px;
	border: solid 4px #10264b;
	border-radius: 12px;
	position: relative;
}

.settings-form-group {
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 60%;
	grid-gap: 4px;
	gap: 4px;
}

.settings-form-group label {
	align-self: flex-start;
}

.settings-form-control {
	width: 100%;
	background-color: #fff;
	border: 2px solid #fff;
	border-radius: 12px;
	padding: 10px 20px;
}

.errorMessage {
	font-size: 12px;
	color: #d24141;
	position: absolute;
	bottom: 10px;
}

.btn-settings {
	background-color: #d24141;
	color: #fff;
	border: 2px solid #d24141;
	width: 60%;
}

.btn-settings:hover {
	background-color: #fff;
	color: #d24141;
}

.manage-container {
	display: flex;
    flex-direction: column;
    grid-gap: 8px;
    gap: 8px;
}

.manage-car{
    display: grid;
    grid-template-columns: 280px 1fr;
    width: 100%;
    border: 1px solid #10264b;
    border-radius: 12px;
    overflow: hidden;
}

.manage-info{
    display: flex;
    flex-direction: column;
    justify-content: center;
    grid-gap: 6px;
    gap: 6px;
    align-items: start;
    background-color: #f6f6f6;
    padding: 12px;
    border-radius: 12px 0 0px 12px;
}

.manage-info--rentalID{
    font-weight: 600;
}

.manage-details{
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #fff;
    padding: 12px 24px;
    border-radius: 0 12px 12px 0;
}

.manage-details--img{
    max-width: 180px;
    border: 1px solid #ddd;
    border-radius: 12px;
}

.manage-details--info{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    grid-gap: 6px;
    gap: 6px;
    align-items: start;
    padding: 12px;
    border-radius: 12px 0 0px 12px;
    flex-shrink: 0;
}

.btn-manage {
    width: 100%
}

.confirm {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    grid-gap: 4px;
    gap: 4px;
}

.confirm-text{
    font-size: 12px;
    align-self: center;
    font-weight: 600;
    color: #d24141;
}
